<script>
export default {
	data() {
		return {
			ruleForm: {
			},

            pageFlag : '',
			tableName:"",
			rules: {},
            jiaoshixingbieOptions: [],
            xueshengxingbieOptions: [],
            xueshengzhuanyeOptions: [],
            xueshengyuanxiOptions: [],
		};
	},
	mounted(){
        this.pageFlag = this.$storage.get("pageFlag");
		let table = this.$storage.get("loginTable");
		this.tableName = table;
		if(this.tableName=='jiaoshi'){
			this.ruleForm = {
                jiaoshigonghao: '',
                jiaoshixingming: '',
                mima: '',
                xingbie: '',
                touxiang: '',
                dianhuahaoma: '',
			}
		}
		if(this.tableName=='xuesheng'){
			this.ruleForm = {
                xueshengzhanghao: '',
                xueshengxingming: '',
                mima: '',
                touxiang: '',
                xingbie: '',
                dianhuahaoma: '',
                zhuanye: '',
                yuanxi: '',
                youxiang: '',
                banji: '',
			}
		}
		if ('jiaoshi' == this.tableName) {
			this.rules.jiaoshigonghao = [{ required: true, message: '', trigger: 'blur' }]
		}
		if ('jiaoshi' == this.tableName) {
			this.rules.jiaoshixingming = [{ required: true, message: '', trigger: 'blur' }]
		}
		if ('jiaoshi' == this.tableName) {
			this.rules.mima = [{ required: true, message: '', trigger: 'blur' }]
		}
		if ('xuesheng' == this.tableName) {
			this.rules.xueshengzhanghao = [{ required: true, message: '', trigger: 'blur' }]
		}
		if ('xuesheng' == this.tableName) {
			this.rules.xueshengxingming = [{ required: true, message: '', trigger: 'blur' }]
		}
		if ('xuesheng' == this.tableName) {
			this.rules.mima = [{ required: true, message: '', trigger: 'blur' }]
		}
        this.jiaoshixingbieOptions = ",".split(',')
        this.xueshengxingbieOptions = ",".split(',')
        this.$http({
            url: `option/zhuanye/zhuanye`,
            method: "get"
        }).then(({ data }) => {
            if (data && data.code === 0) {
                this.xueshengzhuanyeOptions = data.data;
            } else {
                this.$message.error(data.msg);
            }
        });
        this.$http({
            url: `option/yuanxi/yuanxi`,
            method: "get"
        }).then(({ data }) => {
            if (data && data.code === 0) {
                this.xueshengyuanxiOptions = data.data;
            } else {
                this.$message.error(data.msg);
            }
        });
	},
	created() {},
	destroyed() {
		  	},
	methods: {
		changeRules(name){
			if(this.rules[name]){
				return true
			}
			return false
		},
		// uuid
		getUUID () {
			return new Date().getTime();
		},
		close(){
			this.$router.push({ path: "/login" });
		},
        jiaoshitouxiangUploadChange(fileUrls) {
            this.ruleForm.touxiang = fileUrls;
        },
        xueshengtouxiangUploadChange(fileUrls) {
            this.ruleForm.touxiang = fileUrls;
        },

        // 


		// 
		login() {
			var url=this.tableName+"/register";
					if((!this.ruleForm.jiaoshigonghao) && `jiaoshi` == this.tableName){
						this.$message.error(``);
						return
					}
					if((!this.ruleForm.jiaoshixingming) && `jiaoshi` == this.tableName){
						this.$message.error(``);
						return
					}
					if((!this.ruleForm.mima) && `jiaoshi` == this.tableName){
						this.$message.error(``);
						return
					}
					if((this.ruleForm.mima!=this.ruleForm.mima2) && `jiaoshi` == this.tableName){
						this.$message.error(``);
						return
					}
            if(this.ruleForm.touxiang!=null) {
                this.ruleForm.touxiang = this.ruleForm.touxiang.replace(new RegExp(this.$base.url,"g"),"");
            }
					if(`jiaoshi` == this.tableName && this.ruleForm.dianhuahaoma &&(!this.$validate.isMobile(this.ruleForm.dianhuahaoma))){
						this.$message.error(``);
						return
					}
					if((!this.ruleForm.xueshengzhanghao) && `xuesheng` == this.tableName){
						this.$message.error(``);
						return
					}
					if((!this.ruleForm.xueshengxingming) && `xuesheng` == this.tableName){
						this.$message.error(``);
						return
					}
					if((!this.ruleForm.mima) && `xuesheng` == this.tableName){
						this.$message.error(``);
						return
					}
					if((this.ruleForm.mima!=this.ruleForm.mima2) && `xuesheng` == this.tableName){
						this.$message.error(``);
						return
					}
            if(this.ruleForm.touxiang!=null) {
                this.ruleForm.touxiang = this.ruleForm.touxiang.replace(new RegExp(this.$base.url,"g"),"");
            }
					if(`xuesheng` == this.tableName && this.ruleForm.dianhuahaoma &&(!this.$validate.isMobile(this.ruleForm.dianhuahaoma))){
						this.$message.error(``);
						return
					}
					if(`xuesheng` == this.tableName && this.ruleForm.youxiang &&(!this.$validate.isEmail(this.ruleForm.youxiang))){
						this.$message.error(``);
						return
					}
			this.$http({
				url: url,
				method: "post",
				data:this.ruleForm
			}).then(({ data }) => {
				if (data && data.code === 0) {
					this.$message({
						message: "",
						type: "success",
						duration: 1500,
						onClose: () => {
							this.$router.replace({ path: "/login" });
						}
					});
				} else {
					this.$message.error(data.msg);
				}
			});
		}
	}
};
